.acivity-container {
    position: relative;
}

.acivity-container img {
    border-radius: 11px;
}

.acivity-container h2 {
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    top: 0;
    width: 75%;
    font-size: 0.9rem;
    color: white;
    font-weight: 900;
    text-shadow: 1px 1px 12px black;
}

.acivity-container a {
    position: absolute;
    text-align: center;
    border-radius: 10px;
    bottom: 10px;
    left: 0;
    right: 0;
    margin: auto;
    width: 70%;
    font-size: 0.6rem;
    border: none;
    color: white;
}

.acivity-container a:hover,
.acivity-container a:focus,
.acivity-container a:active {
    opacity: 0.9;
    color: white;
    border: none;
    outline: none;
    box-shadow: none;
}

@media only screen and (min-width: 345px) {
    .acivity-container a {
        font-size: 1rem;
    }
    .acivity-container h2 {
        font-size: 1.2rem;
    } 
}

@media only screen and (min-width: 380px) {
    .acivity-container h2 {
        font-size: 1.5rem;
    }  
}

@media only screen and (min-width: 576px) {
    .acivity-container h2 {
        font-size: 1.6rem;
    }
}

@media only screen and (min-width: 992px) {
    .acivity-container h2 {
        font-size: 1.3rem;
    }
}

@media only screen and (min-width: 1200px) {
    .acivity-container h2 {
        font-size: 1.6rem;
    }
}