.foto-container {
    background-color: white;
    box-shadow: 0px 4px 6px rgb(48 145 161);
    padding: 10px;
    width: 100%;
    -webkit-column-break-inside: avoid;
    /* Chrome, Safari, Opera */
    page-break-inside: avoid;
    /* Firefox */
    break-inside: avoid;
    /* IE 10+ */
    -webkit-backface-visibility: hidden;
    /* code to smooth edges when rotating*/
}

.foto-container img {
    width: 100%;
}

.foto-container h6 {
    font-family: 'Rancho', cursive;
    font-size: 0.7rem;
}

.fotos .right {
    transform: rotate(3deg);
}

.fotos .left {
    transform: rotate(-3deg);
}

@media only screen and (min-width: 576px) {
    .foto-container h6 {
        font-size: 0.9rem;
    }
}

@media only screen and (min-width: 992px) {
    .fotos .right {
        transform: rotate(2deg);
    }

    .fotos .left {
        transform: rotate(-1deg);
    }
}