
.inicio-hacemos-container .fa {
    color: white;
}

.inicio-hacemos-container h5 {
    text-transform: uppercase;
    font-family: 'Wolesbro', cursive;
    color: white;
    font-size: 1.5rem;
    text-shadow: 0px 1px 1px rgb(93, 93, 93);
}

.inicio-hacemos-container .fa {
    font-size: 2.5rem;
    text-shadow: 0px 1px 1px rgb(93, 93, 93);
}

.inicio-hacemos-container .btn {
    background-color:transparent;
    border: 2px solid white;
    color: white;
    text-transform: uppercase;
    width: 150px;
    cursor: pointer;
}

.inicio-hacemos-container .btn:hover {
    background-color: #ffffff31;
}
